/* Map Container */
.map-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #526b5c;
  box-sizing: border-box;
  height: 100%;
}

.map-section {
  width: 100%;
  height: -webkit-fill-available;
  position: relative;
}

.form-popup {
  position: fixed; /* Changed from absolute to fixed */
  z-index: 1002; /* Higher than search bar (1001) */
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.12);
}

/* Search Bar */
.search-bar {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 360px;
  height: 3.5rem;
  z-index: 1001;
}

.search-bar-container {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  border-radius: 28px;
  padding: 10px 15px;
  width: 100%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
  position: relative;
  overflow: hidden;
}

.search-input {
  flex: 1;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 400;
  color: #333;
  background: transparent;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.clear-search {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  color: #888;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
}

.pac-container {
  width: 360px !important;
  border-radius: 0 0 28px 28px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
  font-size: 16px;
  overflow: hidden;
  margin-top: 5px;
}

.pac-item {
  padding: 10px 15px;
  font-size: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.overlay.show {
  position: fixed;
  width: 100%;
  z-index: 1002;
  background: rgba(0, 0, 0, 0.5);
  top: 0px;
  height: 100%;
}

.community-feed-container {
  position: absolute;
  top: 10px;
  right: 1rem;
  height: -webkit-fill-available;
  z-index: 1001;
  display: flex;
  flex-direction: column;
  align-items: center;
  user-select: none;
}

.community-dropdown {
  width: 18rem;
  height: 3.5rem;
  z-index: 1001;
  border-radius: 25px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 300;
  padding: 0 1.5rem;
}
