body {
    font-family: 'Poppins', 'Helvetica', sans-serif;
}

button {
    padding: 0;
    border: none;
    background: none;
}

a {
    color: white;
}

.qADiv {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem;
    padding-right: 1rem;
    padding-left: 1rem;
    padding-bottom: 0.5rem;
    background-color: #214664;
    color: white;
    border-radius: 2em;
    box-shadow: 2px 2px 4px 2px rgb(110, 120, 126);
    width: 75%;
}

.contactLayout {
    display: grid;
    grid-template-columns: 2fr 5fr;
    color: white;
}

.iconCenter {
    margin: auto;
    width: 75%;
    text-align: left;
    padding-right: 1em;
}

.contactImg {
    text-align: center;
    align-items: center;
    background-color: skyblue;
     display: block;
      margin: auto
}
.questionHead {
    display: grid;
    grid-template-columns: 5fr 1fr;
    justify-content: right;
    margin-bottom: 0em;
    padding-bottom: 0em;
}

.categoryHeader {
    color: #214664;
    font-size: x-large;
    font-weight: bold;
    margin-right: auto;
    margin-left: auto;
    width: 75%;
}

.contactSource {
    margin-right: auto;
    margin-left: auto;
    display: grid;
    font-family: 'Poppins', 'Helvetica', sans-serif;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 1rem;
    align-items: center;
    max-width: max-content;
}


.contactSourceDiv {
    background-color: #214664;
    color: white;
    width: 150px;
    height: 150px;
    padding: 2rem;
    border-radius: 10em;
    align-items: center;
    text-align: center;
    box-shadow: 2px 2px 4px 2px rgb(110, 120, 126);
    overflow-wrap: break-word;
}

.hidden { 
    display: none;
}

.contactHeader {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 1rem;
    align-items: center;
}

.disclaimer {
    color: #214664;
    font-style: italic;
    font-size: large;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
}

@media (max-width: 950px) {
    .contactSource {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

/*medium screen, two columns*/
@media (max-width: 768px) {
    .qADiv, .categoryHeader, .disclamer {
        width: 80%;
    }
    .categoryHeader {
        width: 80%;
    }
    .contactSource {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

/* Small Screen: 1 Column */
@media (max-width: 480px) {
    .qADiv, .categoryHeader, .disclaimer {
        width: 90%;
    }
    .categoryHeader {
        width: 90%;
    }
    .contactSource {
        display: grid;
        grid-template-columns: 1fr;
    }
}

.imgBorder {
    border-radius: 50%;
    background-color: #214664;
    margin: 0.25em;
    border-width: 0em;
    float: right;
}








