.annotation-form {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.12);
  padding: 24px;
  width: 340px;
  position: relative;
  border: 1px solid #e0e0e0;
  font-family: 'Segoe UI', system-ui, sans-serif;
  box-sizing: border-box;
  overflow: hidden;
}

.form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid #eeeeee;
}

.form-header h3 {
  color: #214664;
  font-size: 18px;
  font-weight: 600;
  margin: 0;
}

.close-btn {
  background: none;
  border: none;
  color: #666666;
  font-size: 24px;
  line-height: 1;
  padding: 0;
  cursor: pointer;
  transition: color 0.2s;
}

.close-btn:hover {
  color: #444;
}

.form-group {
  margin-bottom: 20px;
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  font-size: 14px;
  color: #444444;
  margin-bottom: 8px;
  font-weight: 500;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 10px 14px;
  border: 1px solid #dddddd;
  border-radius: 8px;
  font-size: 14px;
  color: #333333;
  background: #f8f9fa;
  transition: border-color 0.2s;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #214664;
  box-shadow: 0 0 0 2px rgba(33, 70, 100, 0.1);
}
.form-group .checkbox-wrapper,
.form-group .radio-wrapper {
  display: flex;
  align-items: center;
  margin: 0.8rem 0;
}

/* Radio and Checkbox styling */
.form-group div {
  display: flex;
  align-items: center;
  margin: 8px 0;
}

.form-group input[type="checkbox"],
.form-group input[type="radio"] {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  border: 2px solid #214664;
  border-radius: 3px;
  margin: 0 12px 0 0;
  position: relative;
  cursor: pointer;
}

.form-group input[type="checkbox"]:checked::before,
.form-group input[type="radio"]:checked::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  background: #214664;
  border-radius: 2px;
}

.form-group input[type="radio"] {
  border-radius: 50%;
}

.form-group input,
.form-group textarea,
.form-group select {
  box-sizing: border-box;
  max-width: 100%;
}

/* File Upload Section */
.image-upload-container {
  border: 2px dashed #ddd;
  border-radius: 8px;
  padding: 1.5rem;
  text-align: center;
  margin: 1rem 0;
}

.upload-instructions {
  color: #666;
  font-size: 0.9rem;
  margin: 0.5rem 0;
}

/* Form Navigation */
.form-navigation {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.form-navigation button {
  flex: 1;
  text-align: center;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  background: #214664;
  color: white;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
  order: 2; /* Default order for Next/Submit */
}

.form-navigation button[type="button"]:first-child {
  order: 1; /* Back button */
  background: #f0f0f0;
  color: #444;
}

/* First page specific styling */
.annotation-form[data-page="1"] .form-navigation button {
  order: 1;
  margin-right: auto;
}

.form-navigation button:hover {
  background: #17324d;
}

.form-navigation button[type="button"]:first-child:hover {
  background: #e0e0e0;
}

.email-alderman-wrapper {
  display: flex;
  align-items: center;
  margin: 2rem 0 1.5rem;
  padding: 1.2rem;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
}

.email-alderman-wrapper input[type="checkbox"] {
  margin-right: 12px;
  width: 20px;
  height: 20px;
}

.email-alderman-wrapper label {
  color: #444;
  font-size: 15px;
  font-weight: 500;
}

/* Add subtle transition */
.email-alderman-wrapper {
  transition: all 0.2s ease;
}

.email-alderman-wrapper:hover {
  background: #f3f4f6;
  border-color: #d1d5db;
}

.annotation-form[data-page="1"] .form-navigation {
  justify-content: flex-end;
}

.annotation-form[data-page="1"] .form-navigation button {
  order: 2;
  width: auto;
}