/* General Styles */
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap");

.navbar {
  position: sticky;
  top: 0;
  left: 0;
  background-color: #ffffff;
  padding: 10px 20px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  z-index: 3000;
  font-family: "Inter", sans-serif;
}

/* Navbar Container */
.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1512px;
  margin: 0 auto;
}

.nav-left {
  display: flex;
  align-items: center;
}

.nav-logo {
  text-decoration: none;
  color: #000000;
  font-weight: 600;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav-icon {
  font-size: 24px;
  color: #000000;
}

.nav-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

.nav-link {
  text-decoration: none;
  color: #000000;
  font-size: 16px;
  font-weight: 400;
}

.nav-link:hover {
  text-decoration: underline;
}

.nav-link.active-link {
  text-decoration: underline;
}

.nav-user-icon {
  font-size: 24px;
  color: #000000;
}
