.image-container,
.image-upload-container {
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer !important;
  width: 100%;
}

.image-container {
  gap: 20px;
}

.image-upload-container {
  border: 2px dashed #ddd;
}

.upload-label {
  padding: 20px;
  cursor: pointer;
  font-weight: bold;
}

.image-display {
  position: relative;
  width: 100%;
  height: 200px;
}

.image-display img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.browse-button {
  font-size: 12px !important;
  color: green !important;
  border: 1px green solid !important;
  background: white !important;
  border-radius: 5px !important;
  padding: 10px !important;
}

.nav-button {
  position: absolute;
  top: 50%;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-button.left {
  left: 10px;
  transform: translateY(-50%);
}

.nav-button.right {
  right: 10px;
  transform: translateY(-50%);
}

.image-counter {
  position: absolute;
  bottom: 10px;
  right: 10px;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 12px;
}
